.login-page {
    background-color: rgb(16, 16, 29);
    font-family: Poppins, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    }
    
    .form-container {
    background-color: rgb(36, 36, 36);
    padding: 1rem 2rem;
    border-radius: 10px;
    box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.2);
    animation: fadeIn 0.5s ease;
    width: 30%;
    margin: 0 auto;
    align-items: center;
    justify-content: center;
    margin-top: 1vh;
    height: 50vh;
    display: flex;
    flex-direction: column;
    opacity: 0.9;
    }

    .form-container:hover{
        opacity: 1;
    }
    
    .form-container h1 {
    color: white;
    font-weight: bold;
    }
    
    .form-container h2 {
    color: white;
    font-weight: 200;
    font-size: 20px;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 35px;
    }
    
    form {
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
    }
    
    .form-group {
    width: 100%;
    margin-bottom: 0.8rem;
    align-items: center;
    justify-content: center;
    display: flex;
    text-align: center;
    }
    
    input[type="text"], input[type="password"] {
    padding: 0.7rem;
    border-radius: 5px;
    border: none;
    background-color: rgb(54, 54, 54);
    box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.2);
    color: white;
    animation: fadeIn 0.5s ease;
    margin-bottom: 6px;
    width: 300px;
    text-align: left;
    padding-left: 5px;
    outline: none;
    margin-left: 8px;
    }
    
    ::placeholder {
    color: rgb(211, 211, 211);
    font-weight: 200;
    position: absolute;
    margin-left: 7px;
    }
    
    input[type="submit"] {
    padding: 0.5rem 1rem;
    border-radius: 5px;
    background-color: rgb(22, 55, 243);
    box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.2);
    color: rgb(248, 248, 248);
    border: none;
    font-weight: bold;
    transition: background-color 0.2s ease;
    text-align: center;
    margin-top: 1px;
    width: 200px;
    font-weight: 200;
    }
    
    input[type="submit"]:hover {
    cursor: pointer;
    background-color: rgb(25, 60, 214);
    box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.3);
    color: white;
    }
    
    @keyframes fadeIn {
    from {
    opacity: 0;
    }
    to {
    opacity: 1;
    }
    }