.dashboard-container {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(18, 19, 27);
}

.admin-text {
    position: fixed;
    font-size: 20px;
    margin-top: 10px;
    margin-left: 20px;
    font-weight: bold;
    }
    
    .menu-open-bg{
        background-color: red;
    }

.fade-enter {
    opacity: 0;
    transform: translateX(-100%);
  }
  .fade-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: all 0.3s ease-in-out;
  }
  .fade-exit {
    opacity: 1;
    transform: translateX(0);
  }
  .fade-exit-active {
    opacity: 0;
    transform: translateX(-100%);
    transition: all 0.3s ease-in-out;
  }
  

#menu {
    width: 3%;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    overflow-y: auto;
    transition: width 0.3s ease-in-out;
    z-index: 1;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.7);
    background-color: rgb(16, 15, 26);
    transition: all 0.3s ease-in-out;
    display: flex;
    justify-content:left;
    overflow: hidden;
    font-size: 20px;
    text-align: left;
}

#menu-btn{
    top: 0;
    left: 0;
    position: fixed;
    margin-left: 0.12%;
    align-self: flex-start;
    opacity: 0.9;
}

#user-btn{
    position: fixed;
    bottom: 0;
    left: 0;
    margin-bottom: 1%;
    background: transparent;
    border: none;
    font-size: 35px;
    color: white;
    opacity: 0.8;
    font-weight: lighter;
}

#menu.menu-open {
    width: 12.5%;
    background-color: rgb(16, 15, 26);
}

.menu-header {
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}


.menu-item {
    padding: 20px;
    transition: background-color 0.3s ease-in-out;
    display: flex;
    align-items: center;
    justify-content: center;
}


.menu-item a {
    margin-top: 100px;
    color: #fff;
    text-decoration: none;
}

#menu-btn {
    background-color: transparent;
    border: none;
    font-size: 20px;
    text-align: center;
    color: rgb(245, 245, 245);
    transition: all 0.3s ease-in-out;
    display: flex;

    align-items: center;
    justify-content: center;
    }
    
    #menu-btn:hover {
    transform: scale(1.1);
    }
    
    #menu-btn:active {
    transform: scale(0.9);
    }
    
    .menu-header h5 {
    font-weight: bold;
    margin: 0;
    padding: 0;
    color: #fff;
    }
    
    .menu-content {
    position: fixed;
    top: 0;
    left: 0;
    margin-left: 2.4%;
    text-decoration: none;
    list-style: none;
    background-color: rgb(16, 15, 26);
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    }

    
    .menu-item a:hover {
    color: #f0f0f0;
    text-decoration: none;
    }
    
    .menu-item a:active {
    color: #f0f0f0;
    text-decoration: none;
    }
    
    .menu-item a:focus {
    color: #f0f0f0;
    text-decoration: none;
    }
    
    .menu-item a:visited {
    color: #f0f0f0;
    text-decoration: none;
    }